.iframe-page {
  height: calc(100vh - 90px) !important;

  padding: 20px;
  margin-top: 50px;
  background: $whith-bg;
  flex: 1;
  overflow-y: auto;
  outline: none!important;
  &:focus{
    outline: none !important;
  }
  iframe{
    border-radius: 10px;
    width: 100%;
    height: 100%;
    border: none;
    outline: none !important;
    outline-style:none;
    box-shadow:none;
    border-color:transparent;
    .moqup-title{
      display: none;
    }
    &:focus{
      outline: none !important;
    }
  }
}